//alert.vue
<template>
    <transition name="fade" @after-leave="afterLeave" >
        <div class="alert" v-if="show">
            <div class="window">
                <h4 v-if="title && !custom" class="title">
                    <span class="text">{{title}}</span>
                    <button v-if="hasClose" class='close' @click="close">
                        <div class='blocker'></div>
                        <i>&#x0042</i>
                    </button>
                </h4>
                <slot name="title"></slot>
                <p v-if="content" class="content">{{content}}</p>
                <slot name="content"></slot>
                <div class="btn-container">
                    <btn @click.native="onClick('cancel',$event)" v-if='cancel' :className='"reverse gray"'>{{cancel}}</btn>
                    <btn @click.native="onClick('ok',$event)" v-if='ok' :className='"reverse gray"'>{{ok}}</btn>
                </div>
                <slot name="button"></slot>
            </div>
        </div>
    </transition>
</template>
<script>
    import Vue from 'vue';
    import base from '../base.vue';
    import btn from '../button/button.vue'
    export default Vue.extend({
        mixins: [base],
        props: ['custom'],
        components: {
            btn
        },
        data () {
            return {
                show: false,
                'title': '',
                'content': '',
                'cancel': '',
                'ok': '',
                hasClose: false,
            }
        },
        methods: {
            onClick (msg,e) {
                this.$emit(msg,e);
                if(msg == 'cancel'){
                    this.close();
                }
            },
            created () {
                this.mountMyself();
            },
            close () {
                this.show = false;
            },
            afterLeave () {
                this.$destroy();
            }
        },
        mounted () {
            this.$nextTick( () => {
                this.show = true;
            })
        }
    })
</script>

<style lang='less'>
    @import '../less/var.less';
    @import '../less/layout.less';

    .alert {
        position: fixed;
        top: 0;
        left: 0;
        z-index: @z-max+100;
        height: 100%;
        width: 100%;
        background-color: rgba(0,0,0,0.6);
        .flexbox();
        .flexbox.center();
        .window {
            width: 80%;
            max-width: 520px;
            background-color: @white;
            .title {
                font-size: .32rem;
                text-align: center;
                line-height: 1.22rem;
                color: @gray-dark;
                .flexbox();
                .text {
                    .flexitem(1);
            }
            }
            .close {
                color: @gray-dark;
                position: absolute;
                -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
                .blocker {
                    .flexitem(1);
                    display: block;
                    content: ' ';
                    height: 100%;
                    width: 100%;
                    -webkit-box-flex: 1;
                }
                i {
                    .font-icon();
                    display: block;
                    width: .7rem;
                    height: 100%;
                    line-height: .88rem;
                }
            }
            .content {
                text-align: center;
                font-size: .32rem;
                padding: .4rem;
                color: @gray-dark;
            }
            .btn-container {
                .flexbox();
                .btn {
                    .flexitem(1);
                    height: .88rem;
                    font-size: .32rem;
                    background-color: @white;
                    color: @gray-dark;
                    line-height: .88rem;
                    text-align:center;
                    border: none;
                    border-radius: 0;
                    border-top: @border-nm;
                    &:first-child:not(:only-child) {
                        border-right: @border-nm;
                    }
                }
            }
        }

    }
</style>
<style scoped>
    .fade-enter-active,
    .fade-leave-active {
      transition: all .5s;
    }
    .fade-enter,
    .fade-leave-active {
      opacity: 0;
    }
    .fade-enter .window {
        -webkit-transform: translate(0%, 25%);
        -moz-transform: translate(0%, 25%);
        transform: translate(0%, 25%);
        -webkit-transition-timing-function: ease-in;
        -moz-transition-timing-function: ease-in;
        transition-timing-function: ease-in;
    }
    .fade-leave-active .window {
        -webkit-transform: translate(0%, -25%);
        -moz-transform: translate(0%, -25%);
        transform: translate(0%, -25%);
        -webkit-transition-timing-function: ease-in;
        -moz-transition-timing-function: ease-in;
        transition-timing-function: ease-in;
    }
    .fade-enter-active .window,
    .fade-leave-active .window{
        transition: all .5s;
    }
</style>


