<template>
    <div class="gome-ui-img-container">
        <img :src="'data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw=='" v-lazyload="src">
    </div>
</template>
<script>
    import Vue from 'vue';
    import Lazyload from 'gome-ui-lazyload';
    Vue.use(Lazyload);
    export default Vue.extend({
        props: ['src']
    });
</script>
<style lang='less'>
    .gome-ui-img-container {
        background: url('../images/default_bg_transparent.png') no-repeat;
        background-position: center center;
        background-size: 80% auto; 
    }
</style>


