<template>
    <span class="ui tag" ><slot name="icon"></slot><slot></slot></span>
</template>
<script>
export default ({
})
</script>
<style lang="less">
    @import "../less/var.less";
    @import "../less/layout.less";
    .ui {
        &.tag {
            &.default {
                display: inline-block;
                font-size: @font-nm-sm;
                border: 1px solid @red;
                color: @red;
            }
            &.solid {
                display: inline-block;
                font-size: @font-sm;
                background-color: @red;
                padding: .03rem .04rem;
                color: @white;
                border-radius: 2px;
            }
            &.icon-tag {
                box-sizing: content-box;
                display: inline-block;
                font-size: @font-nm-sm;
                border: 1px solid @red;
                color: @red;
            }
        }
    }
</style>



