//tabnav.scroll.vue
<template>
    <div class='aaa'>
        <div class='scroller-container' :class="[direction,className]">
            <div class='scroller'
            :style='[transform,transition]'
            :class="className"
            @touchstart='touchstart'
            @touchmove='touchmove'
            @touchend='touchend'
            @transitionEnd='transitionEnd'
             >
                <slot></slot>
            </div>
        </div>
    </div>
</template>
<script>
import swiperMixin from "../swiper/swiper.mixin.vue";
import base from '../base.vue';
export default {
    props: ['options','list','class','direction'],
    mixins: [swiperMixin],
    data () {
        return {
            maxTranslate: 0,
            contentWidth: 0,
            $container: null,
            $scroller: null,
            wrapperLimit: 0,
        }
    },
    created () {
        this.initSwiper()
        this.$on('scroll', (e) => {
            this.scroll(e);
        });
        this.$on('scrollEnd', (e) => {
            this.scrollEnd(e);
        });
    },
    mounted () {
        this.$container = this.$el.querySelector('.scroller-container') || this.$el;
        this.$scroller = this.$el.querySelector('.scroller');
        this.computeTransLimit();
        this.$watch('list', () => {
            this.computeTransLimit();
        })
    },
    methods: {
        scroll () {
            this.computeTransLimit();
            if(this.contentWidth < this.wrapperLimit)return;
            if(this.translate < -this.maxTranslate || this.translate > 0){
                this.translate += this.delta/2.5
            }else{
                this.translate += this.delta
            }    
        },
        scrollEnd () {
            if(this.translate > 0){
                this.translate = 0
            }
            if(this.translate < -this.maxTranslate){
                this.translate = -this.maxTranslate
            }
        },
        computeTransLimit () {
            let children = this.$scroller.children
            this.contentWidth = 0
            this.wrapperLimit = this.direction == 'horizontal' ? this.$container.offsetWidth : this.$container.offsetHeight
            for(let i=0; i<children.length; i++){
                this.contentWidth += children[i].offsetWidth
            }
            if(this.contentWidth < this.wrapperLimit)
                this.maxTranslate = 0
            else
                this.maxTranslate = this.contentWidth - this.wrapperLimit
        },
    }
};
</script>
<style lang='less'>
    .scroller-container {
        overflow: hidden;
        &.vertical {
            position: absolute;
            width: 100%;
            .scroller {
                position: absolute;
                width: 100%;
            }
        }
        &.horizontal {
            .scroller {
                display: -webkit-box;
            }
        }
    }
</style>



