<script>
    if(global['ctmCid'] !== 0){
        global['ctmCid'] = 0;
    }
    export default {
        props: ['className'],
        methods: {
            mountMyself (id = 'module') {
                this.$nextTick(() => {
                    let $blocker = document.createElement('div');
                    global['ctmCid']++;
                    $blocker.id = id + '-ctm-' + global['ctmCid'];
                    document.body.appendChild($blocker);
                    this.$mount('#'+$blocker.id);
                });
            },
            destroyMyself () {
                this.$destroy();
            }
        },
        beforeDestroy () {
            this.$el.parentNode.removeChild(this.$el);
        }
    }
</script>


